import React, { Fragment, Component } from "react";
import { Form } from "./HomepageForm/index";
import { Header } from "./Layouts";
import Login from "./Layouts/NewLogin";
import { withStyles } from "@material-ui/core/styles";
import { Route, Link, BrowserRouter as Router } from "react-router-dom";
import { MuiThemeProvider, createMuiTheme } from "@material-ui/core";
import Results from "./Layouts/Results";
import Team from "./Layouts/Team";
import About from "./Layouts/About";
import "typeface-roboto";

const theme = createMuiTheme({
  palette: {
    primary: {
      main: "#222831"
    },
    secondary: {
      main: "#393e46"
    }
  }
});

const styles = {};

class App extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <MuiThemeProvider theme={theme}>
        <Router>
          <Fragment>
            <Header />
            <Route exact path={process.env.PUBLIC_URL + "/"} component={Form} />
            <Route path={process.env.PUBLIC_URL + "/login"} component={Login} />
            <Route
              path={process.env.PUBLIC_URL + "/results"}
              component={Results}
            />
            <Route path={process.env.PUBLIC_URL + "/team"} component={Team} />
            <Route path={process.env.PUBLIC_URL + "/about"} component={About} />
          </Fragment>
        </Router>
      </MuiThemeProvider>
    );
  }
}

export default withStyles(styles)(App);
